import { useEffect, useRef, useState } from 'react'

export const isFalsy = (value: unknown): boolean =>
  value === 0 ? false : !value

export const isVoid = (value: unknown) =>
  value === undefined || value === null || value === ''

// 在一个函数里，改变传入的对象本身是不好的
export const cleanObject = (object: Record<string, unknown>) => {
  const result = { ...object }
  Object.keys(result).forEach((key) => {
    const value = result[key]
    if (isVoid(value)) delete result[key]
  })
  return result
}

export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback()
    // TODO 依赖项里加上 callback 会造成无线循环，这个和 useCallback 以及 useMemo 有关系
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
}

// 后面用泛型来规范类型
export const useDebounce = <V>(value: V, delay: number = 200): V => {
  const [debouncedValue, setDebouncedValue] = useState(value)
  useEffect(() => {
    // 每次在 value 变化以后，设置一个定时器
    const timeout = setTimeout(() => setDebouncedValue(value), delay)
    // 每次在上一个 useEffect 处理完成以后再运行
    return () => clearTimeout(timeout)
  }, [value, delay])
  return debouncedValue
}

export const useArray = <T>(initialArray: T[]) => {
  const [value, setValue] = useState(initialArray)
  return {
    value,
    setValue,
    add: (item: T) => setValue([...value, item]),
    clear: () => setValue([]),
    removeIndex: (index: number) => {
      if (!value.length) return
      const copy = [...value]
      copy.splice(index, 1)
      setValue(copy)
    },
  }
}

export const useDocumentTitle = (
  title: string,
  keepOnUnmount: boolean = true,
) => {
  const oldTitle = useRef(document.title).current
  // 页面加载时: oldTitle === 旧 title ->'React App'
  // 加载后: oldTtile === 新 title

  useEffect(() => {
    document.title = title
  }, [title])
  useEffect(() => {
    return () => {
      if (!keepOnUnmount) {
        // 如果不指定依赖，读到的就是旧 title
        document.title = oldTitle
      }
    }
  }, [oldTitle, keepOnUnmount])
}

export const resetRoute = () => (window.location.href = window.location.origin)

/**
 * 用来返回组件加载状态，如果还没挂在或者已经卸载，返回 false 反之返回 true
 */
export const useMountedRef = () => {
  const mountedRef = useRef(false)
  useEffect(() => {
    mountedRef.current = !0
    return () => {
      mountedRef.current = !1
    }
  })
  return mountedRef
}
