import { createSlice } from '@reduxjs/toolkit'
import { AuthForm, User } from 'types'
import * as auth from 'auth-provider'
import { AppDipsatch, RootState } from 'store'
import { bootstrapUser } from 'context/auth-context'

interface State {
  user: User | null
}
type FnType = (form?: AuthForm) => any

const initialState: State = {
  user: null,
}

export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setUser(state, action) {
      state.user = action.payload
    },
  },
})

const { setUser } = authSlice.actions
export const selectUser = (state: RootState) => state.auth.user

export const loginThunk: FnType = (form) => (dispatch: AppDipsatch) =>
  auth.login(form!).then((user) => dispatch(setUser(user)))

export const registerThunk: FnType = (form) => (dispatch: AppDipsatch) =>
  auth.register(form!).then((user) => dispatch(setUser(user)))

export const logoutThunk: FnType = () => (dispatch: AppDipsatch) =>
  auth.logout().then(() => dispatch(setUser(null)))

export const bootstrapThunk: FnType = () => (dispatch: AppDipsatch) =>
  bootstrapUser().then((user) => dispatch(setUser(user)))
