import qs from 'qs'
import * as auth from 'auth-provider'
import { useAuth } from 'context/auth-context'
import { useCallback } from 'react'

console.log(process.env)

const apiUrl = process.env.REACT_APP_API_URL
console.log(apiUrl)

interface Config extends RequestInit {
  data?: Record<string, any>
  token?: string
}
export type ParametersHttpParams = Parameters<typeof http>

export const http = async (
  endpoint: string,
  { data, token, headers, ...customConfig }: Config = {},
) => {
  const trimData = (data: any) =>
    !data.name && !data.personId
      ? {}
      : !data.personId
      ? { name: data.name }
      : data
  const config = {
    method: 'GET',
    headers: {
      Authorization: token ? `Bearer ${token}` : '',
      'Content-Type': data ? 'application/json' : '',
    },
    ...customConfig,
  }
  data = endpoint === 'projects' && data ? trimData(data) : data

  if (config.method.toUpperCase() === 'GET') {
    endpoint += `?${qs.stringify(data)}`
  } else config.body = JSON.stringify(data)

  // axios 和 fetch 表现不一样，axiso 可以直接返回状态不为2xx的时候抛出异常
  return window
    .fetch(`${apiUrl}/${endpoint}`, config)
    .then(async (response) => {
      if (response.status === 401) {
        await auth.logout()
        window.location.reload()
        return Promise.reject({ message: '请重新登录' })
      }
      const data = await response.json()
      return response.ok ? data : Promise.reject(data)
    })
}

export const useHttp = () => {
  const { user } = useAuth()
  return useCallback(
    (...[endpoint, config]: ParametersHttpParams) =>
      http(endpoint, { ...config, token: user?.token }),
    [user?.token],
  )
}
