import styled from '@emotion/styled'
import { ButtonNoPadding, Row } from 'components/lib'
import { ProjectListScreen } from 'screens/project-list'
import { ReactComponent as SoftwareLogo } from 'assets/software-logo.svg'
import { Button, Dropdown, Menu } from 'antd'
import { Navigate, Routes, Route } from 'react-router'
import { BrowserRouter as Router } from 'react-router-dom'
import { ProjectScreen } from 'screens/project'
import { resetRoute } from 'utils'
import { ProjectModal } from 'screens/project-list/project-modal'
import { ProjectPopover } from 'components/project-popover'
import { useDispatch, useSelector } from 'react-redux'
import { logoutThunk, selectUser } from 'store/auth.slice'
import { UserPopover } from 'components/user-popover'
// import { ProjectScreen } from 'screens/project'

// https://github.com/bvaughn/react-error-boundary
export const AuthenticatedApp = () => {
  return (
    <Container>
      <Router>
        <PageHeader />
        <Main>
          <Routes>
            <Route path={'/projects'} element={<ProjectListScreen />} />
            <Route
              path={'/projects/:projectId/*'}
              element={<ProjectScreen />}
            />
            <Navigate to={'/projects'} />
          </Routes>
        </Main>
        <ProjectModal />
      </Router>
    </Container>
  )
}

const PageHeader = () => {
  return (
    <Header
      params={{
        between: true,
      }}
    >
      <HeaderLeft
        params={{
          gap: true,
        }}
      >
        {['Logo', '项目', '组员'].map((item, idx) =>
          item === 'Logo' ? (
            <ButtonNoPadding type="link" onClick={resetRoute} key={idx}>
              <SoftwareLogo width="18rem" color="rgb(38,132,255)" />
            </ButtonNoPadding>
          ) : item === '项目' ? (
            <ProjectPopover key={idx} />
          ) : (
            item === '组员' && <UserPopover key={idx} />
          ),
        )}
      </HeaderLeft>
      <HeaderRight>
        <User />
      </HeaderRight>
    </Header>
  )
}

const User = () => {
  const dispatch = useDispatch()
  const user = useSelector(selectUser)
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={'logout'}>
            <Button type="link" onClick={dispatch.bind(null, logoutThunk())}>
              登出
            </Button>
          </Menu.Item>
        </Menu>
      }
    >
      <Button type="link">Hi, {user?.name}</Button>
    </Dropdown>
  )
}

/**
 * grid 和 flex 各自的应用场景
 * 1. 要考虑，是 一维布局 还是 二维布局
 *    一维布局: 整个布局内容都为单一的横向布局或者单一的纵向布局
 *    二维布局: 横纵向交叉布局
 * 一般来说，一维布局用 flex，二维布局用 grid
 * 2. 是从内容出发还是从布局出发？
 *    从内容出发: 先有一组内容(数量一般不固定（数组遍历或者未来要添加新的元素）)，然后希望他们均匀的分布在容器中，由内容自己的大小决定战局的空间
 *    从布局出发: 先规划网格(一般来说数据比较固定)，然后再把元素往里填充
 * 从内容出发，使用 flex
 * 从布局出发，用 grid
 */

const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr 6rem;
  height: 100vh;
`

// grid-area 用来给 grid 子元素起名字
const Header = styled(Row)`
  padding: 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  z-index: 1;
`
const HeaderLeft = styled(Row)``
const HeaderRight = styled.div``
const Main = styled.main`
  display: flex;
  overflow: hidden;
`
