import styled from '@emotion/styled'
import { Button, Drawer, Form, Input, Spin } from 'antd'
import { useForm } from 'antd/lib/form/Form'
import { ErrorBox, Row } from 'components/lib'
import { UserSelect } from 'components/user-select'
import { useEffect } from 'react'
import { useAddProject, useEditProject } from 'utils/project'
// import { useDispatch, useSelector } from 'react-redux'
// import { projectListAction, selectProjectMpdalOpen } from './project-list.slice'
import { useProjectModal, useProjectsQueryKey } from './util'

export const ProjectModal = () => {
  const { projectModalOpen, close, editingProject, isLoading } =
    useProjectModal()
  const useMutateProject = editingProject ? useEditProject : useAddProject
  const {
    mutateAsync,
    error,
    isLoading: mutaoLoading,
  } = useMutateProject(useProjectsQueryKey())
  const [form] = useForm()
  const onFinish = (values: any) => {
    mutateAsync({ ...editingProject, ...values }).then(() => {
      form.resetFields()
      close()
    })
  }
  const closeModal = () => {
    form.resetFields()
    close()
  }
  const title = `${editingProject ? '编辑' : '创建'}项目`

  useEffect(() => {
    form.setFieldsValue(editingProject)
  }, [editingProject, form])

  return (
    <Drawer
      forceRender={true}
      onClose={closeModal}
      visible={projectModalOpen}
      width="100%"
    >
      <Row
        style={{ flexDirection: 'column' }}
        params={{
          between: 'center',
        }}
      >
        {isLoading ? (
          <ExampleSpin size="large" />
        ) : (
          <>
            <h1>{title}</h1>
            <ErrorBox error={error} />
            <Form
              form={form}
              layout="vertical"
              style={{ width: '40rem' }}
              onFinish={onFinish}
            >
              <Form.Item
                label="名称"
                name="name"
                rules={[{ required: true, message: '请输入项目名' }]}
              >
                <Input placeholder="请输入项目名称" />
              </Form.Item>
              <Form.Item
                label="部门"
                name="organization"
                rules={[{ required: true, message: '请输入部门名' }]}
              >
                <Input placeholder="请输入部门名称" />
              </Form.Item>
              <Form.Item label="负责人" name="personId">
                <UserSelect defaultOptionName="负责人"></UserSelect>
              </Form.Item>
              <Form.Item style={{ textAlign: 'right' }}>
                <Button loading={mutaoLoading} type="primary" htmlType="submit">
                  提交
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
      </Row>
    </Drawer>
  )
  // const dispatch = useDispatch()
  // const projectModalOpen = useSelector(selectProjectMpdalOpen)
  // return (
  //   <Drawer
  //     onClose={dispatch.bind(null, projectListAction.clooseProjectModal())}
  //     visible={projectModalOpen}
  //     width="100%"
  //   >
  //     <h1>Project Modal</h1>
  //     <Button
  //       onClick={dispatch.bind(null, projectListAction.clooseProjectModal())}
  //     >
  //       关闭
  //     </Button>
  //   </Drawer>
  // )
}

const ExampleSpin = styled(Spin)`
  & > span {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
`
