import { Dropdown, Menu, Modal, Table, TableProps } from 'antd'
import { ListProps as typesListProps, Project } from 'types'
import dayjs from 'dayjs'
import { Link } from 'react-router-dom'
import { Pin } from 'components/pin'
import { useDeleteProject, useEditProject } from 'utils/project'
import { ButtonNoPadding } from 'components/lib'
import { useProjectModal, useProjectsQueryKey } from './util'
// react-router 和 react-router-dom的关系，类似于 react 和 react-dom react-native

interface ListProps extends TableProps<any>, typesListProps {}

export const List = ({ users, ...props }: ListProps) => {
  const { mutate } = useEditProject(useProjectsQueryKey())
  const pinProject = (id: number) => (pin: boolean) => mutate({ id, pin })
  return (
    <Table
      pagination={false}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render(value, project) {
            return (
              <Pin
                checked={project.pin}
                onCheckedChange={pinProject(project.id)}
              />
            )
          },
        },
        {
          title: '名称',
          align: 'center',
          sorter: (a, b) => a.name.localeCompare(b.name),
          render(value, project) {
            return <Link to={String(project.id)}>{project.name}</Link>
          },
        },
        {
          title: '部门',
          dataIndex: 'organization',
          align: 'center',
        },
        {
          title: '负责人',
          align: 'center',
          render: (value, project) => (
            <span>
              {users.find((user) => user.id === project.personId)?.name ||
                '未知'}
            </span>
          ),
        },
        {
          title: '创建时间',
          align: 'center',
          render: (value, project) => (
            <span>
              {(project.created &&
                dayjs(project.created).format('YYYY-MM-DD')) ||
                ''}
            </span>
          ),
        },
        {
          render(value, project) {
            return <More project={project} />
          },
        },
      ]}
      rowKey={(record) => record.id}
      {...props}
    />
  )
}

const More = ({ project }: { project: Project }) => {
  const { startEdit } = useProjectModal()
  const editProject = (id: number) => () => startEdit(id)
  const { mutate: deleteProject } = useDeleteProject(useProjectsQueryKey())
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: '确定删除这个项目吗？',
      content: '点击确定删除',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        deleteProject({ id })
      },
    })
  }
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key="edit">
            <ButtonNoPadding type="link" onClick={editProject(project.id)}>
              编辑
            </ButtonNoPadding>
          </Menu.Item>
          <Menu.Item key="delete">
            <ButtonNoPadding
              type="link"
              onClick={confirmDeleteProject.bind(null, project.id)}
            >
              删除
            </ButtonNoPadding>
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type="link">...</ButtonNoPadding>
    </Dropdown>
  )
}
