import { useMemo } from 'react'
import { URLSearchParamsInit, useSearchParams } from 'react-router-dom'
import { cleanObject } from 'utils'

/**
 * 返回页面 url 中指定键的参数值
 */
export const useUrlQueryParam = <K extends string>(keys: K[]) => {
  const [serachParams] = useSearchParams()
  const setSerachParams = useSetUrlSearchParam()
  return [
    useMemo(
      () =>
        keys.reduce(
          (prev, key: string) => ({
            ...prev,
            [key]: serachParams.get(key) || '',
          }),
          {} as Record<K, string>,
        ),
      // eslint-disable-next-line react-hooks/exhaustive-deps
      [serachParams],
    ),
    (params: Partial<{ [Key in K]: unknown }>) => setSerachParams(params),
  ] as const
}

export const useSetUrlSearchParam = () => {
  const [serachParams, setSerachParam] = useSearchParams()
  return (params: Record<string, unknown>) =>
    setSerachParam(
      cleanObject({
        ...Object.fromEntries(serachParams),
        ...params,
      }) as URLSearchParamsInit,
    )
}
