import { List } from './list'
import { SearchPanel } from './search-panel'
import { useDebounce, useDocumentTitle } from '../../utils/index'
import styled from '@emotion/styled'
import { useProjects } from 'utils/project'
import { useUsers } from 'utils/user'
import { useProjectModal, useProjectSearchParams } from './util'
import { ButtonNoPadding, ErrorBox, Row } from 'components/lib'
// import { useDispatch } from 'react-redux'
// import { projectListAction } from './project-list.slice'

export const ProjectListScreen = () => {
  useDocumentTitle('项目列表', false)
  const [param, setParam] = useProjectSearchParams()
  const { open } = useProjectModal()
  let { isLoading, error, data: list } = useProjects(useDebounce(param, 200))
  const { data: users } = useUsers()
  list = list?.sort((a, b) => b.created - a.created)
  // const dispatch = useDispatch()

  return (
    <Container>
      <Row
        params={{
          between: true,
        }}
      >
        <h1>项目列表</h1>
        <ButtonNoPadding type="link" onClick={open}>
          创建项目
        </ButtonNoPadding>
        {/* <ButtonNoPadding
          onClick={dispatch.bind(null, projectListAction.openProjectModal())}
        >
          创建项目
        </ButtonNoPadding> */}
      </Row>
      <SearchPanel param={param} setParam={setParam} users={users || []} />
      <ErrorBox error={error} />
      <List loading={isLoading} dataSource={list || []} users={users || []} />
    </Container>
  )
}

ProjectListScreen.whyDidYouRender = false

const Container = styled.div`
  width: 100vw;
  padding: 3.2rem;
`
