import { useCallback } from 'react'
import * as auth from 'auth-provider'
import { AuthForm, ProviderFnType, User } from 'types'
import { http } from 'utils/http'
import { useMount } from 'utils'
import { UseAsync } from 'utils/use-async'
import { FullPageErrorCllback, FullPageLoading } from 'components/lib'
import * as authStore from 'store/auth.slice'
import { useDispatch, useSelector } from 'react-redux'
import { bootstrapThunk, selectUser } from 'store/auth.slice'
import { useQueryClient } from 'react-query'

export const bootstrapUser = async () => {
  let user = null
  const token = auth.getToken()
  if (token) {
    const data = await http('me', { token })
    user = data.user
  }
  return user
}

export const AuthProvider: ProviderFnType = ({ children }) => {
  const { error, isLoading, isIdle, isError, run } = UseAsync<User | null>()
  const dispatch: (...args: unknown[]) => Promise<User> = useDispatch()
  useMount(() => {
    run(dispatch(bootstrapThunk()))
  })

  if (isIdle || isLoading) return <FullPageLoading />
  if (isError)
    return <FullPageErrorCllback error={error as Error}></FullPageErrorCllback>
  return children
}

export const useAuth = () => {
  const dispatch: (...args: unknown[]) => Promise<User> = useDispatch()
  const user = useSelector(selectUser)
  const queryClient = useQueryClient()
  const login = useCallback(
    (form: AuthForm) => dispatch(authStore.loginThunk(form)),
    [dispatch],
  )
  const register = useCallback(
    (form: AuthForm) => dispatch(authStore.registerThunk(form)),
    [dispatch],
  )
  const logout = useCallback(() => {
    dispatch(authStore.logoutThunk())
    queryClient.clear()
  }, [dispatch, queryClient])
  return {
    user,
    login,
    register,
    logout,
  }
}
