import { Kanban } from 'types/kanban'
import { useTasks } from 'utils/taks'
import taskIcon from 'assets/task.svg'
import { useTaskTypes } from 'utils/task-type'
import { useKanbansQueryKey, useTasksModal, useTasksSearchParams } from './util'
import bugIcon from 'assets/bug.svg'
import styled from '@emotion/styled'
import { Button, Card, Dropdown, Menu, Modal } from 'antd'
import { CreateTask } from './create-task'
import { Task } from 'types/task'
import { Mark } from 'components/mark'
import { ButtonNoPadding, Row } from 'components/lib'
import { useDeleteKanban } from 'utils/kanban'
import React from 'react'
import { Drag, Drop, DropChild } from 'components/drag-and-drop'

const TaskTypeIcon = ({ id }: { id: number }) => {
  const { data: taskTypes } = useTaskTypes()
  const name = taskTypes?.find((taskType) => taskType.id === id)?.name
  if (!name) return null
  return <img src={name === 'task' ? taskIcon : bugIcon} alt="" />
}

export const KanbanColumn = React.forwardRef<
  HTMLDivElement,
  { kanban: Kanban }
>(({ kanban, ...props }, ref) => {
  const { data: allTasks } = useTasks(useTasksSearchParams())
  const tasks = allTasks?.filter((task) => task.kanbanId === kanban.id)
  return (
    <Container ref={ref} {...props}>
      <Row
        params={{
          between: true,
        }}
      >
        <h3>{kanban.name}</h3>
        <More kanban={kanban} key={kanban.id} />
      </Row>
      <TasksContainer>
        <Drop type="ROW" direction="vertical" droppableId={`${kanban.id}`}>
          <DropChild style={{ minHeight: '1rem' }}>
            {tasks?.map((task, taskIndex) => (
              <Drag
                key={task.id}
                index={taskIndex}
                draggableId={`task${task.id}`}
              >
                <div>
                  <TaskCard task={task} key={task.id} />
                </div>
              </Drag>
            ))}
          </DropChild>
        </Drop>
        <CreateTask kanbanId={kanban.id} />
      </TasksContainer>
    </Container>
  )
})

const TaskCard = ({ task }: { task: Task }) => {
  const { startEdit } = useTasksModal()
  const { name: keyword } = useTasksSearchParams()
  return (
    <Card
      onClick={startEdit.bind(null, task.id)}
      key={task.id}
      style={{ marginBottom: '0.5rem', cursor: 'pointer' }}
    >
      <Row
        params={{
          between: true,
        }}
      >
        <Mark name={task.name} keyword={keyword} />
        <TaskTypeIcon id={task.typeId} />
      </Row>
    </Card>
  )
}

const More = ({ kanban }: { kanban: Kanban }) => {
  const { mutateAsync } = useDeleteKanban(useKanbansQueryKey())
  const startEdit = () => {
    Modal.confirm({
      okText: '确定',
      cancelText: '取消',
      title: '确定删除看板吗？',
      onOk() {
        return mutateAsync({ id: kanban.id })
      },
    })
  }
  const overlay = (
    <Menu>
      <Menu.Item key={kanban.id}>
        <ButtonNoPadding type="link" onClick={startEdit}>
          删除
        </ButtonNoPadding>
      </Menu.Item>
    </Menu>
  )
  return (
    <Dropdown overlay={overlay}>
      <Button type="link">...</Button>
    </Dropdown>
  )
}

export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  flex-direction: column;
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 1.5rem;
`
const TasksContainer = styled.div`
  overflow: scroll;
  flex: 1;
  ::-webkit-scrollbar {
    display: none;
  }
`
