import { useCallback, useReducer, useState } from 'react'
import { useMountedRef } from 'utils'

enum StatusEnum {
  IDLE = 'idle',
  LOADING = 'loading',
  ERROR = 'error',
  SUCCESS = 'success',
}

interface State<D> {
  error: Error | null
  data: D | null
  stat: StatusEnum
}

const defaultInitialState: State<null> = {
  stat: StatusEnum.IDLE,
  data: null,
  error: null,
}

const defaultConfig = {
  throwOnError: false,
}

const useSafeDispatch = <T>(dispatch: (...args: T[]) => void) => {
  const mountedRef = useMountedRef()
  return useCallback(
    (...args: T[]) => (mountedRef.current ? dispatch(...args) : void 0),
    [mountedRef, dispatch],
  )
}

export const UseAsync = <D>(
  initialState?: State<D>,
  initialConfig?: typeof defaultConfig,
) => {
  const config = { ...defaultConfig, ...initialConfig }
  const [state, dispatch] = useReducer(
    (state: State<D>, action: Partial<State<D>>) => ({ ...state, ...action }),
    {
      ...defaultInitialState,
      ...initialState,
    },
  )
  const safeDispatch = useSafeDispatch(dispatch)

  // useState 直接传入函数的含义是：惰性初始化，所以要用 useState 保存函数时，不能直接传入函数
  const [retry, setRetry] = useState(() => () => {})

  const setData = useCallback(
    (data: D) =>
      safeDispatch({
        data,
        stat: StatusEnum.SUCCESS,
        error: null,
      }),
    [safeDispatch],
  )

  const setError = useCallback(
    (error: Error) =>
      safeDispatch({
        error,
        stat: StatusEnum.ERROR,
        data: null,
      }),
    [safeDispatch],
  )

  // run 用来触发异步请求的
  const run = useCallback(
    (promise: Promise<D>, runConfig?: { retry: () => Promise<D> }) => {
      if (!promise || !promise.then) {
        throw new Error('请传入 Promise 类型数据')
      }
      setRetry(() => () => run(runConfig!.retry(), runConfig))
      safeDispatch({ stat: StatusEnum.LOADING })
      return promise
        .then((data) => {
          setData(data)
          return data
        })
        .catch((error) => {
          // catch 会消化异常，如果不主动抛出，外面是接收不到异常的
          setError(error)
          return config.throwOnError ? Promise.reject(error) : error
        })
    },
    [safeDispatch, config.throwOnError, setData, setError],
  )

  return {
    isIdle: state.stat === StatusEnum.IDLE,
    isLoading: state.stat === StatusEnum.LOADING,
    isError: state.stat === StatusEnum.ERROR,
    isSuccess: state.stat === StatusEnum.SUCCESS,
    run,
    setData,
    setError,
    // retry 被调用时，重新跑一遍 run，让 states 刷新一遍
    retry,
    ...state,
  }
}
