import { useMemo } from 'react'
import { useProject } from 'utils/project'
import { useSetUrlSearchParam, useUrlQueryParam } from 'utils/url'

export const useProjectSearchParams = () => {
  const [param, setParam] = useUrlQueryParam(['name', 'personId'])
  return [
    useMemo(() => ({ ...param, personId: Number(param.personId) }), [param]),
    setParam,
  ] as const
}

export const useProjectsQueryKey = () => [
  'projects',
  useProjectSearchParams()[0],
]

export const useProjectModal = () => {
  const [{ projectCreate }, setProjectCreate] = useUrlQueryParam([
    'projectCreate',
  ])
  const [{ editingProjectId }, setEditingProjectId] = useUrlQueryParam([
    'editingProjectId',
  ])
  const setUrlParams = useSetUrlSearchParam()
  const { data: editingProject, isLoading } = useProject(+editingProjectId)
  const startEdit = (id: number) =>
    setEditingProjectId({ editingProjectId: id })
  const open = () => setProjectCreate({ projectCreate: !0 })
  // const close = () => {
  //   setProjectCreate({ projectCreate: undefined })
  //   editingProjectId && setEditingProjectId({ editingProjectId: undefined })
  // }
  const close = () => setUrlParams({ projectCreate: '', editingProjectId: '' })
  return {
    open,
    close,
    startEdit,
    editingProject,
    isLoading,
    projectModalOpen: projectCreate === 'true' || !!editingProjectId,
  } as const
}
