import { Button, List, Modal } from 'antd'
import { Row, ScreenContainer } from 'components/lib'
import dayjs from 'dayjs'
import { useState } from 'react'
import { Link } from 'react-router-dom'
import { useProjectInUrl } from 'screens/kanban/util'
import { Epic } from 'types/epic'
import { useDeleteEpic, useEpics } from 'utils/epic'
import { useTasks } from 'utils/taks'
import { CreateEpic } from './crteate-epic'
import { useEpicSearchParams, useEpicsQueryKey } from './util'

export const EpicScreen = () => {
  const { data: currentProject } = useProjectInUrl()
  const { data: epics } = useEpics(useEpicSearchParams())
  const { data: tasks } = useTasks({ projectId: currentProject?.id })
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey())
  const [epicCreateOpen, setEpicCreateOpen] = useState(false)
  const cofirmDeleteEpic = (epic: Epic) => {
    Modal.confirm({
      title: `确定删除项目组: ${epic.name}吗？`,
      content: '点击确定删除',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        deleteEpic({ id: epic.id })
      },
    })
  }
  return (
    <ScreenContainer>
      <Row
        params={{
          between: true,
        }}
      >
        <h1>{currentProject?.name}任务组</h1>
        <Button type="link" onClick={setEpicCreateOpen.bind(null, true)}>
          创建任务组
        </Button>
      </Row>
      <List
        style={{ display: 'flex', flex: '1', overflow: 'auto' }}
        dataSource={epics}
        itemLayout="vertical"
        renderItem={(epic) => (
          <List.Item>
            <List.Item.Meta
              title={
                <Row
                  params={{
                    between: true,
                  }}
                >
                  <span>{epic.name}</span>
                  <Button
                    type="link"
                    onClick={cofirmDeleteEpic.bind(null, epic)}
                  >
                    删除
                  </Button>
                </Row>
              }
              description={
                <div>
                  <div>
                    开始时间：{dayjs(epic.start).format('YYYY-MM-DD HH:mm:ss')}
                  </div>
                  <div>
                    结束时间：{dayjs(epic.end).format('YYYY-MM-DD HH:mm:ss')}
                  </div>
                </div>
              }
            />
            <div>
              {tasks
                ?.filter((task) => task.epicId === epic.id)
                .map((task) => (
                  <Link to={`/kanban?editingTaskId=${task.id}`} key={task.id}>
                    {task.name}
                  </Link>
                ))}
            </div>
          </List.Item>
        )}
      />
      <CreateEpic
        onClose={setEpicCreateOpen.bind(null, false)}
        visible={epicCreateOpen}
        projectId={currentProject!.id}
      />
    </ScreenContainer>
  )
}
